@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.button {
    font: 1em Arial, sans-serif;
    display: inline-block;
    padding: 1em;margin: 1em;
  /*   border-radius: 5px; */
    text-decoration: none;

    
    /* Set up the hover */
    /* If you aren't using autoprefix, remember to prefix the gradient for other browsers */
    background-image: linear-gradient(dodgerblue, dodgerblue);
    background-size: 0 5px, auto;
    background-repeat: no-repeat;
    background-position: center bottom;
    /* transition: all 1s ease-out; */
    border-color: '#f4a203';
    /* transform: scaleX(0);
    transition: transform 0.3s ease; */
  }
  
  .button:hover {
    /* The following line makes the underline only as wide as the text */
    /* background-size: calc(100% - 2em) 5px, auto; */
    background-size: 100% 5px, auto;
    border-color: '#f4a203',
  }

  .subHeading {
    text-align: -webkit-center;
  }

  .blob {
    border-radius: 50%;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-animation: circle-pulse 3s infinite;
            animation: circle-pulse 3s infinite;
  }
  
  @-webkit-keyframes circle-pulse {
    0% {
      -webkit-transform: scale(0.95);
              transform: scale(0.95);
      /* box-shadow: 0 0 0 0 rgba(121, 115, 115, 0.151); */
    }
    
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* box-shadow: 0 0 0 5px rgba(160, 149, 149, 0); */
    }
    
    100% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      /* box-shadow: 0 0 0 0 rgba(177, 163, 163, 0); */
    }
  }
  
  @keyframes circle-pulse {
    0% {
      -webkit-transform: scale(0.95);
              transform: scale(0.95);
      /* box-shadow: 0 0 0 0 rgba(121, 115, 115, 0.151); */
    }
    
    50% {
      -webkit-transform: scale(1.2);
              transform: scale(1.2);
      /* box-shadow: 0 0 0 5px rgba(160, 149, 149, 0); */
    }
    
    100% {
      -webkit-transform: scale(0.3);
              transform: scale(0.3);
      /* box-shadow: 0 0 0 0 rgba(177, 163, 163, 0); */
    }
  }

  /* To remove the margins on window.print() */
  @media print {
    @page {
       margin-left: 0.5in;
       margin-right: 0.5in;
       margin-top: 0;
       margin-bottom: 0;
     }
}

.text1 {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  min-height:80px;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.heading1 {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
